*{
  list-style: none;
  text-decoration: none;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Open Sans', sans-serif;
}

body{
  background: #f5f6fa;
}


.navbar ul li a{
  display: block;
  padding: 13px 30px;
  border-bottom: 1px solid #10558d;
  color: rgb(6, 5, 5);
  font-size: 16px;
  position: relative;
}

.navbar ul li a:hover,
.navbar ul li a.active{
  color: #0c7db1;

  background:#021e0f;
  border-right: 2px solid rgb(5, 68, 104);
}

.navbar ul li a:hover:before,
.navbar ul li a.active:before{
  display: block;
}
.Wrapper.navbar .item{
  margin-top: -100px;
}

p{
  margin-top: 50px;
  size: 30px;
}
.main{
  display: flex;
  width: 100%;
  margin-right: -10%;
  
}
.orders{
  margin-top: 1px;
  text-transform: capitalize;
}
.orders-list{
  width: 100%;
  padding: 10px;
  margin-top: 10px;
  background: #fff;
  border-radius: 10px;
  box-shadow: 0 20px 35px rgba(0, 0, 0, 0.1);
}
.table{
  border-collapse: collapse;
  margin: 25px 0;
  font-size: 15px;
  min-width: 77%;
  margin-left: 2%;
  overflow: hidden;
  border-radius: 5px 5px 0 0;
}
table thead tr{
  color: #fff;
  background: #34AF6D;
  text-align: left;
  font-weight: bold;
}
.table th,
.table td{
  padding: 12px 15px;
}
.table tbody tr{
  border-bottom: 1px solid #ddd;
}
.table tbody tr:nth-of-type(odd){
  background: #f3f3f3;
}
.table tbody tr.active{
  font-weight: bold;
  color: #4AD489;
}
.table tbody tr:last-of-type{
  border-bottom: 2px solid #4AD489;
}
.table button{
  padding: 6px 20px;
  border-radius: 10px;
  cursor: pointer;
  background: transparent;
  border: 1px solid #4AD489;
}
.table button:hover{
  background: #4AD489;
  color: #fff;
  transition: 0.5rem;



}
